<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>客户管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">客户信用(授信)查询</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看客户信用(授信)</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline">
        <el-form-item label="销售组织:" size="mini">
          <span class="szstyle"> {{params.company_name}}</span>
        </el-form-item>
        <el-form-item label="客户名称:" size="mini">
          <span class="szstyle">{{params.customer_name}}</span>
        </el-form-item>
        <el-form-item label="客户分类:" size="mini">
          <span class="szstyle">{{params.customer_type_name}}</span>
        </el-form-item>

        <el-form-item label="年度" size="mini">
          <el-date-picker v-model="params.start_date" type="year" value-format="yyyy" :clearable="false" placeholder="选择年" @change="gettime">
          </el-date-picker>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" max-height="600" style="width: 100%">
        <el-table-column :show-overflow-tooltip="true" property="customer_name" label="客户名称"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="credit_type_name" label="授信类型"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="credit_time" label="授信时间"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="credit_balance" label="授信金额" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.credit_balance  | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="credit_occupying_price" label="授信占用金额" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.credit_occupying_price  | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="use_credit" label="使用授信金额" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.use_credit  | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="return_credit" label="回款授信金额" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.return_credit  | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="non_return_credit" label="未回款授信金额" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.non_return_credit  | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="surplus_credit" label="剩余可使用授信金额" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.surplus_credit  | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="business_sn" label="单据号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="add_time" label="单据时间/审核时间"></el-table-column>
      </el-table>
    </div>
    <div class="page_container">
      <div class="pageNumber">每页显示</div>
      <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
      </el-pagination>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack">返 回</el-button>
    </div>
  </div>
</template>

<script>
import { getData } from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "customeCreditfindSearch",
  data() {
    return {
      authority: {},
      timeValue: [],
      start_date: 2019,
      params: {},
      TableData: { tableData: [] },
      company: { options: [] },
      customerList: { options: [] },
      is_disableds: false,
      goPage: 1,
      per_page: [30, 50, 100, 500],
      pageSize: 30
    };
  },
  computed: {
    ...mapGetters(["creditfindSearch_showGet"])
  },
  methods: {
    ...mapActions(["creditfindDefault_show", "creditfindIndexACT"]), // wz alter
    gettime() {
      this.$set(this, "searchParams", {
        params: this.params, //1默认带的参数
        TableData: this.TableData, //列表数据
        API: "getOneCustomerCreditList", //接口名
        vm: this //传递的this
      });
    },
    onReset() {
      this.$set(this, "params", {
        page: 1,
        customer_id: this.creditfindSearch_showGet.customer_id,
        company_id: this.creditfindSearch_showGet.company_id,
        company_name: this.creditfindSearch_showGet.company_name,
        customer_name: this.creditfindSearch_showGet.customer_name,
        customer_type_name: this.creditfindSearch_showGet.customer_type_name,
        start_date: new Date()
      });

      let a = this.$publicFution.deepCopy(this.params);
      a.start_date = this.$publicFution.formatDataYear(a.start_date);
      this.$set(this, "searchParams", {
        params: a, //1默认带的参数
        TableData: this.TableData, //列表数据
        API: "getOneCustomerCreditList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams);
    },
    goBack() {
      this.creditfindIndexACT({
        // wz alter
        // this.creditfindDefault_show({
        // commont: "creditfindDefault",
        params: this.creditfindSearch_showGet.params,
        goPage: this.creditfindSearch_showGet.goPage,
        pageSize: this.creditfindSearch_showGet.pageSize
      });
      this.$router.push({
        path: "/custom/creditfind"
      });
    },
    onSearch() {
      Object.assign(this.searchParams.params, {
        page: 1,
        customer_id: this.creditfindSearch_showGet.customer_id,
        company_id: this.creditfindSearch_showGet.company_id
      });
      getData(this.searchParams);
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1,
        customer_id: this.creditfindSearch_showGet.customer_id,
        company_id: this.creditfindSearch_showGet.company_id
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val,
        customer_id: this.creditfindSearch_showGet.customer_id,
        company_id: this.creditfindSearch_showGet.company_id
      });
      getData(this.searchParams);
    }
  },
  computed: {
    ...mapGetters(["creditfindSearch_showGet"])
  },
  created() {
    this.$set(this, "params", {
      customer_id: this.creditfindSearch_showGet.customer_id,
      company_id: this.creditfindSearch_showGet.company_id,
      company_name: this.creditfindSearch_showGet.company_name,
      customer_name: this.creditfindSearch_showGet.customer_name,
      customer_type_name: this.creditfindSearch_showGet.customer_type_name,
      start_date: new Date()
    });

    let a = this.$publicFution.deepCopy(this.params);
    a.start_date = this.$publicFution.formatDataYear(a.start_date);
    this.$set(this, "searchParams", {
      params: a, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getOneCustomerCreditList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams);
  }
};
</script>

<style scoped>
.szstyle {
  width: 200px;
  font-size: 12px;
  line-height: 28px;
  color: #606266;
  display: block;
  word-spacing: normal;
  text-transform: none;
}
